import styles from "./Pools.module.css"
import Timer from "./Timer.js"
import Cart from "./Cart.js";
import Col from 'react-bootstrap/Col'
import Row from 'react-bootstrap/Row'
import Container from 'react-bootstrap/Container'
import classnames from "classnames";
import React, { useState } from "react";

 function Pools(){
  var css1;var css2;
  const [active, setActive] = useState(true);
  if(active==true)
  {
    css1=styles.active;
    css2=styles.inactive;
  }
  else{
    css1=styles.inactive;
    css2=styles.active;
  }

  var css3;var css4;
  const [active1, setActive1] = useState(true);
  if(active1==true)
  {
    css3=styles.active;
    css4=styles.inactive;
  }
  else{
    css3=styles.inactive;
    css4=styles.active;
  }


     return( <Container fluid>
         <div className={styles.body}>
         <div className={styles.stake}>Stake LP Tokens To Earn STABLE</div>
         <Timer/>
       <div className={styles.toggle}>
        <span>
           <button className={classnames(styles.left,css1)} onClick={()=>{setActive(true)}}>Stacked Only</button>
           <button className={classnames(styles.right,css2)} onClick={()=>{setActive(false)}}>All</button>
        </span>
        <span>
           <button className={classnames(styles.left,css3)} onClick={()=>{setActive1(true)}}>Active</button>
           <button className={classnames(styles.right,css4)} onClick={()=>{setActive1(false)}}>Inactive</button>
        </span>
        </div> 
  <Row >
    <Col xs={12} md={6} lg={4}> <Cart value="1" text="STABLE" icon="STABLE" fees="" power="10X" pools="true" deposit="0%" withdrawal="0%"/></Col>
    <Col xs={12} md={6} lg={4}> <Cart value="2" text="USDT" icon="USDT" fees="hide" power="3.5X" pools="true" deposit="3%" withdrawal="0%"/></Col>
    <Col xs={12} md={6} lg={4}> <Cart value="3" text="BUSD" icon="BUSD" fees="hide" power="3X" pools="true" deposit="3%" withdrawal="0%"/></Col>
  </Row>
  <Row >
    <Col xs={12} md={6} lg={4}> <Cart value="1" text="DAI" icon="DAI" fees="hide" power="2X" pools="true" deposit="3%" withdrawal="0%"/></Col>
   {/* <Col xs={12} md={6} lg={4}> <Cart value="2" text="USDC" icon="USDC" fees="hide" power="2X" pools="true" deposit="3%" withdrawal="0%"/></Col>*/}
</Row>

        
         </div></Container>
     );
 }
 export default Pools;