import styles from "./Timer.module.css";
import classnames from "classnames";
import React, { useState,useEffect } from "react";
import stable from "./assets/stablefarms.pdf"
function Timer()
{
  const [remainingTime, setRemainingTime] = useState(getTimeRemaining('Mon Aug 16 2021 14:00:00 GMT'));
  

  useEffect(() => {
    const timer =setInterval(() => {
      setRemainingTime(getTimeRemaining('Mon Aug 16 2021 14:00:00 GMT'));
    }, 1000);
    return () => clearInterval(timer);
  }, []);

    return(
        <div className={classnames(styles.boxTime)}>
        <div className={styles.farming}>FARMS LAUNCH IN</div>
        <div className={styles.row}>
          <div className={classnames(styles.c1, styles.c1_s, styles.c1_m)}>
            <div className={styles.circle}>{remainingTime.days} Days</div>{" "}
          </div>
          <div className={classnames(styles.c2, styles.c2_s, styles.c2_m)}>
            <div className={styles.circle}>{remainingTime.hours} Hours</div>{" "}
          </div>
          <div className={classnames(styles.c3, styles.c3_s, styles.c3_m)}>
            <div className={styles.circle}>{remainingTime.minutes} Minutes</div>{" "}
          </div>
          <div className={classnames(styles.c4, styles.c4_s, styles.c4_m)}>
            <div className={styles.circle}>{remainingTime.seconds} Seconds</div>{" "}
          </div>
        </div>
        <div className={styles.btnbox}><button className={styles.btn} onClick={()=>{window.open(stable, "_blank")}}><i className="fa fa-info"></i> Learn More</button></div>
      </div>
      
    );
}
//Time Start
function getTimeRemaining(endtime) {
  var t = Date.parse(endtime) - Date.parse(new Date());
  var seconds = Math.floor((t / 1000) % 60);
  var minutes = Math.floor((t / 1000 / 60) % 60);
  var hours = Math.floor((t / (1000 * 60 * 60)) % 24);
  var days = Math.floor(t / (1000 * 60 * 60 * 24));
  return {
    'total': t,
    'days': days,
    'hours': hours,
    'minutes': minutes,
    'seconds': seconds
  };
}


export default Timer;