import styles from "./Header.module.css";
import logo from "./assets/logo.png";
import menubtn from "./assets/menubtn.png";
import { useDispatch , useSelector} from "react-redux";
import {connect, readPublicKey} from "./integeration.js"

function Header(props, state) {
  
  console.log("refress")
  const web3=useSelector(state => state.web3)
  const publicKey=useSelector(state=>state.publicKey)
  var text="Connect";
  if(web3!=null)
  {
  text="Disconnect";
  }
  const dispatch = useDispatch();
  
  
  return (
    <div className={styles.bgcolor}>
    <div className={styles.row}>
      <div className={styles.c1}>
        <div className={styles.row1}>
          <div className={styles.c11}>
            <button className={styles.menubtn} onClick={()=>{dispatch({ type: "menuShowChange"})}}>
              <img src={menubtn} className={styles.icon}></img>
            </button>
          </div>
          <div className={styles.c22}>
            <img src={logo} className={styles.logo}></img>
          </div>
        </div>
      </div>
      <div className={styles.c2}>
        <div className={styles.row1}>
          <div className={styles.c11}>
          <div className={styles.address}>{publicKey}</div>
          </div>
          <div className={styles.c22}><button className={styles.connect} onClick={()=>{
            return;
          console.log("click")
        if(text=="Connect")
        {
          connect().then((result)=>{
            if(result.error==false){
              dispatch({ type: "web3Change", web3:result.web3});
              dispatch({ type: "publicKeyChange", publicKey:readPublicKey()});
              dispatch({ type: "approve", approve:null});
              window.ethereum.on('accountsChanged', function (accounts) {
                dispatch({ type: "publicKeyChange", publicKey:readPublicKey()});
                dispatch({ type: "approve", approve:null});
              })
              
            }
          });
        }
        else{
          dispatch({type:"web3Change",web3:null})
          dispatch({type:"publicKeyChange",publicKey:null})
          dispatch({ type: "approve", approve:null});
          
        }
        
        }

      }
  
        
        >{text}</button></div>
        </div>
        
        
        
      </div>
     
    </div> 
    </div>
  );
}
export default Header;
