import styles from './Content.module.css';
import Home from "./Home.js";
import Farms from "./Farms.js"
import Pools from "./Pools.js"
import Prediction from "./Prediction.js"
import Referral from "./Referral.js"
import { useSelector } from "react-redux";

function Content(props,state) {
   
    const menuItem = useSelector(state => state.menuItem);
    if(menuItem=="Home")
    return(<div className={styles.bg}><Home/></div>);
    else if(menuItem=="Farms")
    return(<div className={styles.bg}><Farms/></div>);
    else if(menuItem=="Prediction")
    return(<Prediction/>)
    else if(menuItem=="Referral")
    return(<Referral/>);
    else
    return(<div className={styles.bg}><Pools/></div>);
  };
  export default Content;