function reducer(state = { menuItem: "Home" , menuShow:true, web3:null, publicKey:null,approve:false}, action) {
    switch (action.type) {
      case "menuContentChange":
        return {
          ...state,
         menuItem:action.menuItem
        };

      case "menuShowChange":
          return {
            ...state,
           menuShow:!state.menuShow
      };

      case "web3Change":
          return {
            ...state,
           web3:action.web3
      };

      case "publicKeyChange":
          return {
            ...state,
           publicKey:action.publicKey
      };

      case "approveChange":
          return {
            ...state,
           approve:action.approve
      };

      

      default:
        return state;
    }

  }
  
  export default reducer;