import React from "react";
import {Container, Row, Col, Card, Form, Button } from "react-bootstrap";
import Menu from "./Menu.js";
import Content from "./Content.js";
import Header from "./Header.js";
import Home from "./Home.js"
import styles from "./App.module.css"

import { useSelector } from "react-redux";
import classnames from "classnames";


function App() {
  const menuShow = useSelector(state => state.menuShow);
  var mycss; var mycss2;
  if(menuShow==false)
  { 
    mycss=styles.hide;
    mycss2=styles.wid;
  }
     /*
 
      <Container fluid>
      <Row><Col><Header/></Col></Row>
      <Row xs={4} md={4} lg={4}><Col><Menu/></Col>
      <Col xs={8} md={8} lg={8}><Content/></Col></Row>
    </Container>
   */
 
  return (
    <>
    <div className={styles.wid}>  
      <Header/>
    </div>
    <div className={styles.row}>
      <div className={classnames(styles.c1,mycss)}> 
<Menu/>
      </div>
      <div className={classnames(styles.c2,mycss2)}>
        <Content/>
      </div>
    </div>

       
    </>
   
  );
}

export default App;
